.navBarContainer{
    display: flex;
    height: 80px;
    width: 100%;
    align-items: center;
    justify-content: center;
}

.logoWrapper{
    position: fixed;
    left: 25px;
}

.logo{
    font-family: Syne;
    text-decoration: none;
    color: black;
    font-size: 40px;
    cursor: pointer;
}

.navItems{
    display: flex;
    flex-direction: row;
    width: 60%;
    justify-content: space-evenly;

}

.navItem{
    font-family: Syne;
    font-size: 20px;
    text-decoration: none;
    color: black;
}

.social{
    position: absolute;
    right: 25px;
}
.social img{
    height: 25px;
    width: auto;
    cursor: pointer;
}

.dropdown{
    overflow: hidden;
}

.dropdown .dropBtn{
    font-family: Syne;
    border: none;
    background-color: white;
    color: black;
    outline: none;
    font-size: 20px;
    margin: 0;
}

.dropdownContent{
    display: none;
    position: absolute;
    background-color: white;
}

.dropLink{
    text-decoration: none;
    color: black;
    font-family: Syne;
    font-size: 18px;
    margin: 10px 0;
}

.dropdown:hover .dropdownContent{
    display: flex;
    flex-direction: column;
    
}